@import url(https://maxcdn.bootstrapcdn.com/bootstrap/4.0.0/css/bootstrap.min.css);
@import url(https://fonts.googleapis.com/css?family=Roboto);
*{
  box-sizing: border-box;
}

body {
  background-color: #0c2033;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Roboto', sans-serif;

}

.error {
  width: 100%;
  margin: 40px 0;
  text-align: center;
  color: #d64d96;
}

.percent-raised {
  color: #3cd483;
}

.percent-fallen {
  color: #d64d96;
}

.loading-container {
  width: 100%;
  text-align: center;
  margin: 40px auto;
}

/* Remove close icon from input on IE */
input::-ms-clear {
  display: none;
}

.list-group-item:hover, tr:hover {
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
  box-shadow: 0 8px 24px rgba(32, 43, 54, .25);
}

.Search {
    position: relative;
    width: 30%;
    height: 35px;
    margin: 0 auto;
    padding: 0 20px;
}

@media (max-width: 700px) {
    .Search {
        width: 100%;
    }
}

.Search-icon {
    z-index: 1;
    position: absolute;
    top: 9px;
    left: 28px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 18px;
    height: 18px;
}

.Search-input {
    box-sizing: border-box;
    background-color: #1f364d;
    border-radius: 4px;
    border: 0;
    padding-left: 35px;
    color: white;
    opacity: .8;
    -webkit-transition: opacity .2s;
    transition: opacity .2s;
    width: 100%;
    height: 35px;
}

.Search-input:focus {
    outline: none;
    opacity: 1;
}

.Search ::-webkit-input-placeholder {
    color: #9cb3c9;
    opacity: 1;
}

.Search ::-ms-input-placeholder {
    color: #9cb3c9;
    opacity: 1;
}

.Search ::placeholder {
    color: #9cb3c9;
    opacity: 1;
}

.Search-loading {
    position: absolute;
    top: 9px;
    right: 28px;
}

.Search-result-container {
    position: relative;
    width: 100%;
    max-height: 299px;
    overflow-y: auto;
    background-color: #0f273d;
    border: 1px solid #0c2033;
    border-radius: 4px;
    box-shadow: 0px 0px 40px 0px#1f364d;
    margin-top: 10px;
}

.Search-result {
    color: #9cb3c9;
    padding: 15px 0 15px 35px;
    border-bottom: 2px solid #0c2033;
    cursor: pointer;
}

.Search-result:hover {
    color: #fff;
}

.Search-no-result {
    color: #9cb3c9;
    padding: 15px 0 15px 35px;
    border-bottom: 1px solid #0f273d;
}
.Loading {
    width: 28px;
    height: 28px;
    display: inline-block;
    border: 2px solid #fff;
    border-right-color: transparent;
    border-radius: 50%;
    -webkit-animation: rotate 1s infinite linear;
            animation: rotate 1s infinite linear;
}

@-webkit-keyframes rotate {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes rotate {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.NotFound {
    width: 100%;
    text-align: center;
    margin-top: 60px;
}

.NotFound-title {
    font-weight: 400;
    color: #9cb3c9;
}

.NotFound-link {
    display: inline-block;
    margin-top: 40px;
    color: #fff;
    text-decoration: none;
    border: 1px solid #9cb3c9;
    border-radius: 4px;
    padding: 18px;
    -webkit-transition: border .2s;
    transition: border .2s;
}

.NotFound-link:hover {
    border: 1px solid #fff;
}

.Detail {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    margin-top: 30px;
    margin-bottom: 40px;
    padding: 0 60px;
}

.Detail-heading {
    font-size: 24px;
    font-weight: 300;
}

.Detail-container {
    width: 100%;
    max-width: 400px;
    margin-top: 30px;
    padding: 40px 40px 0;
    border-radius: 4px;
    box-shadow: 0px 0px 40px 0px#1f364d;
}

.Detail-item {
    margin-bottom: 50px;
}

.Detail-value {
    border-radius: 20px;
    background-color: #1f364d;
    font-size: 14px;
    padding: 8px 12px;
    margin-left: 10px;
}

.Detail-title {
    display: block;
    color: #9cb3c9;
    font-size: 12px;
    font-weight: bold;
    margin-bottom: 10px;
}

.Detail-dollar {
    color: #9cb3c9;
    margin-right: 6px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

